import React from 'react';
import { ToastContainer, toast } from 'react-toastify';
import 'rect-toastify/dist/reacttoastify.css'

const Generate = () => {

    const notify = () => toast('Wow so easy !');
    return (
        <div className='bg-gradient-to-r from-blue-700 to-blue-500 min-h-screen grid grid-cols-1 md:grid-cols-2'>
            <Button onClick={notify}>Notify !</Button>
            <ToastContainer />
            <div className="flex justify-center items-center flex-col gap-10 px-6 py-12 md:py-20">
                <h1 className="text-white text-3xl font-bold">Create your Bittree</h1>
                <div className='flex flex-col gap-6 w-full max-w-lg items-center bg-white/10 p-8 rounded-2xl shadow-lg'>
                    <h2 className='text-white text-3xl font-semibold'>Step 1: Claim Your Handle</h2>
                    <input
                        type="text"
                        placeholder='Choose a Handle'
                        className="className='w-full text-lg bg-white p-3 rounded-lg border border-gray-300 focus:outline-none focus:ring-2 focus:ring-blue-300 placeholder-gray-500 shadow-sm'"
                    />
                    <h2 className='text-white text-3xl font-semibold'>Step 2: Add Links</h2>
                    <input
                        type="text"
                        placeholder='Enter link text'
                        className="className='w-full text-lg bg-white p-3 rounded-lg border border-gray-300 focus:outline-none focus:ring-2 focus:ring-blue-300 placeholder-gray-500 shadow-sm'"
                    />
                    <input
                        type="text"
                        placeholder='Enter link'
                        className="className='w-full text-lg bg-white p-3 rounded-lg border border-gray-300 focus:outline-none focus:ring-2 focus:ring-blue-300 placeholder-gray-500 shadow-sm'"
                    />
                    <button className='w-full text-lg py-3 px-6 bg-blue-500 hover:bg-blue-600 transition-all duration-300 rounded-full text-white font-semibold shadow-md'>ADD LINKS</button>
                    <h2 className='text-white text-3xl font-semibold'>Step 3: Add Picture and Final</h2>
                    <input
                        type="text"
                        placeholder='Enter link to your Picture'
                        className="className='w-full text-lg bg-white p-3 rounded-lg border border-gray-300 focus:outline-none focus:ring-2 focus:ring-blue-300 placeholder-gray-500 shadow-sm'"
                    />
                    <button className='w-full text-lg py-3 px-6 bg-blue-500 hover:bg-blue-600 transition-all duration-300 rounded-full text-white font-semibold shadow-md'>CREATE YOUR BITLINK</button>
                </div>
            </div>
            <div className="w-full h-screen">
                <img
                    className="w-full h-full object-cover"
                    src="/generate.png"
                    alt="Generate your links"
                />
            </div>
        </div>
    );
};

export default Generate;
